import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { ProductManageComponent } from './product-manage.component';//产品平台(三期)

export const routes: Routes = [
    {
        path: '',
        component: ProductManageComponent,
        children: [
            { path: 'product-catalogs', loadChildren: './product-catalogs/product-catalogs.module#ProductCatalogsModule' },
            { path: 'module-manage', loadChildren: './module-manage/module-manage.module#ModuleManageModule' },
            { path: 'product-function', loadChildren: './product-function/product-function.module#ProductFunctionModule' },
            { path: 'base-product', loadChildren: './base-product/base-product.module#BaseProductModule' },
            { path: 'product-market', loadChildren: './product-market/product-market.module#ProductMarketModule' },
            { path: 'available-product', loadChildren: './available-product/available-product.module#AvailableProductModule' },
            { path: 'product-summary', loadChildren: './product-summary/product-summary.module#ProductSummaryModule' },
            { path: 'type-management', loadChildren: './type-management/type-management.module#TypeManagementModule' },//类型管理
            { path: 'means-payment', loadChildren: './means-payment/means-payment.module#MeansPaymentModule' },
            { path: 'personalized-information', loadChildren: './personalized-information/personalized-information.module#PersonalizedInformationModule' },//类型管理
            // { path: 'sale-product', loadChildren: './sale-product/sale-product.module#SaleProductModule' },
            // { path: 'product-interview', loadChildren: './product-interview/product-interview.module#ProductInterviewModule' },
            { path: 'product-relation', loadChildren: './product-relation/product-relation.module#ProductRelatModule' },
            // { path: 'product-package', loadChildren: './product-package/product-package.module#ProductPackageModule' },
        ]
    },
];

export const ProductManageRouting: ModuleWithProviders = RouterModule.forChild(routes);

